<template>
	<div class="sendEmail" v-loading="loading">
		<el-steps :active="step" align-center>
			<el-step title="导入Excel数据">
				<i slot="icon" class="iconfont iconexcel" />
			</el-step>
			<el-step title="配置发送邮箱信息">
				<i slot="icon" class="iconfont iconQQyouxiang" />
			</el-step>
			<el-step title="开始发送" icon="el-icon-s-promotion"></el-step>
		</el-steps>
		<div class="main" v-if="step===1">
      <el-upload
        drag
        action=""
        accept=".xlsx"
        :limit="1"
        :auto-upload="false"
        :on-change="changFile"
        class="upload-demo">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或 <em>点击导入</em></div>
      </el-upload>
      <el-table
        highlight-current-row
        :data="form.tableData"
        style="width: 100%">
        <el-table-column
          v-if="headerData.length>0"
          type="index"
          width="50" />
        <el-table-column
          :key="label"
          :prop="label"
          v-for="label in headerData"
          :label="label" />
        <el-table-column
          v-if="headerData.length>0"
          fixed="right"
          label="操作"
          width="120">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="form.tableData.splice(scope.$index,1)"
              type="text"
              size="small">
              移除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
		</div>
		<div class="main set" v-if="step===2">
			<el-form :model="form" :rules="rules" ref="form" label-width="130px">
        <el-form-item label="发送邮件主题" prop="subject">
          <el-input size="small" v-model="form.subject" placeholder="如工资条"></el-input>
        </el-form-item>
        <el-form-item label="发送者QQ邮箱" prop="user">
          <el-input size="small" v-model="form.user" placeholder="请输入发送者QQ邮箱"></el-input>
        </el-form-item>
        <el-form-item label="QQ邮箱授权码" prop="pass">
          <el-input size="small" v-model="form.pass" placeholder="请输入QQ邮箱授权码"></el-input>
          <a target="view_window" href="https://jingyan.baidu.com/article/fedf0737af2b4035ac8977ea.html">如何获取</a>
        </el-form-item>
			</el-form>
		</div>
		<div class="main" v-if="step===3">
			<el-form :inline="true">
        <el-form-item label="发送邮件主题:">{{form.subject}}</el-form-item>
        <el-form-item label="发送者QQ邮箱:">{{form.user}}</el-form-item>
        <el-form-item label="QQ邮箱授权码:">{{form.pass}}</el-form-item>
        <el-form-item label="指定邮箱列:">{{form.emailColumn}}</el-form-item>
      </el-form>
      <el-table
        highlight-current-row
        :data="form.tableData"
        style="width: 100%">
        <el-table-column
          v-if="headerData.length>0"
          type="index"
          width="50" />
        <el-table-column
          :key="label"
          :prop="label"
          v-for="label in headerData"
          :label="label" />
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-tag v-if="status[scope.row[form.emailColumn]]" type="success">发送成功</el-tag>
          </template>
        </el-table-column>
      </el-table>
		</div>
		<div class="footer">
      <el-dropdown v-if="step===1" style="cursor:pointer;margin: 0 10px;">
        <span class="el-dropdown-link">
          {{form.emailColumn || '请指定邮箱列'}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <template v-if="headerData.length>0">
            <el-dropdown-item 
              :key="label"
              @click.native="form.emailColumn=label"
              v-for="label in headerData">{{label}}</el-dropdown-item>
          </template>
          <el-dropdown-item v-else>请先导入文件</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
			<el-button size="mini" v-if="step!==1" @click="step-=1">上一步</el-button>
			<el-button size="mini"  v-if="step!==3" @click="next" type="primary">下一步</el-button>
			<el-button size="mini"  v-if="step===3"  @click="send" type="primary">发送</el-button>
		</div>
	</div>
</template>
<script>
import XLSX from 'xlsx';
import axios from 'axios';
export default {
	data() {
    var regQQ = /^[1-9]\d{4,10}@qq\.com/;
    var validateUser = (rule, value, callback) => {
      if (!regQQ.test(value)) {
        callback(new Error('QQ邮箱格式有误！'));
      } else {
        callback();
      }
    };
		return {
      loading: false,
			step: 1,
      headerData: [],
      form: {
        subject: null,
        tableData: [],//数据
        emailColumn: null,//邮箱列
        user: null,//发送者邮箱
        pass: null,//邮箱授权码
      },
      status: {},
      rules: {
        user: [
          { required: true, message: '请输入QQ邮箱', trigger: 'blur' },
          { validator: validateUser, trigger: 'blur' }
        ],
        pass: [
          { required: true, message: '请输入QQ邮箱授权码', trigger: 'blur' },
        ],
        subject: [
          { required: true, message: '请输入发送邮件主题', trigger: 'blur' },
        ],
      }
		}
  },
	methods: {
		changFile(file) {
      const reader = new FileReader();
      reader.onload = function(e) {
        var wb = XLSX.read(e.target.result, {type: 'binary'});
        const data = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
        this.form.tableData = data;
        const [item] = data;
        this.headerData = item?Object.keys(item):[];
      }.bind(this);
      reader.readAsBinaryString(file.raw);
    },
    validOk() {
      if(this.form.tableData.length===0) {
        this.$message.warning('请导入文件或未检测到文件数据');
        return false;
      }
      if(!this.form.emailColumn) {
        this.$message.warning('请选择指定邮箱列');
        return false;
      }
      const tempObj = {};
      const reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      for(let i=0,j=this.form.tableData.length;i<j;i+=1) {
        const emailVal = this.form.tableData[i][this.form.emailColumn];
        if(!reg.test(emailVal)) {
          this.$message.warning('第 '+(i+1)+' 行邮箱格式不正确');
          return false;
        }
        if(tempObj[emailVal]!==undefined) {
          this.$message.warning('第 '+(i+1)+' 与第 '+tempObj[emailVal]+' 行邮箱重复');
          return false;
        }
        tempObj[emailVal] = i+1;
      }
      return true;
    },
    next() {
      if(this.step===1&&this.validOk()) {
        this.step =2;
      }else if(this.step===2) {
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.step = 3;
          } else {
            return false;
          }
        });
      }
    },
    send() {
      this.$confirm('确定发送邮件吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.loading = true;
          this.status = {};
          axios.post('/send',{
            data: this.form
          }).then((res)=> {
            this.status = res.data || {};
            this.loading = false;
          }).cath(()=> {
             this.loading = false;
          });
        }).catch(() => {         
        });
    }
	}
}
</script>
<style scoped lang='less'>
.sendEmail {
  background: #fff;
	.el-step {
		/deep/ .el-step__icon.is-text {
			border: none;
			width: 40px;
			i {
				font-size: 25px;
			}
			
		}
	}
	.main {
		margin: 20px 180px;
    text-align: center;
    &.set {
      margin: 20px 20%;
    }
	}
	.footer {
		position: fixed;
		right: 50px;
    bottom: 30px;
    z-index: 3;
  }
  .el-form {
    /deep/ .el-form-item__content {
      text-align: left;
      .el-input {
        width: calc(100% - 70px);
        margin-right: 10px;
      }
    }
  }
	
}
</style>